import React, { ReactNode } from 'react';
import { compose } from 'redux';
import { followThunk, getCurrentPage, getCurrentUsersThunk, unFollowThunk } from '../../Redux/user2-reducer';
import { withAuthRedirect } from '../hoc/withAuthRedirect';
import NewUsers from './NewUsers';
import {connect} from 'react-redux';
import Preloader from '../common/preloader/Preloader';
type PropsType = {
    users: Array<object>,
    totalUserCount: number,
    pageSize: number,
    currentPage: number,
    isFetching: boolean,
    fetchingInProgress: Array<any>,
    getCurrentPage: (numberPage: number) => object,
    getCurrentUsersThunk: (pageSize: number, currentPage: number) => object,
    followThunk: (userid: number) => object,
    unFollowThunk: (userid: number) => object
}
class UsersContainer2 extends React.Component<PropsType> {
    componentDidMount() {
        const {pageSize, currentPage} = this.props;
        this.props.getCurrentUsersThunk(pageSize, currentPage);
    }
    changePage(currentPage: number) {
        let {pageSize} = this.props;
        this.props.getCurrentPage(currentPage);
        this.props.getCurrentUsersThunk(pageSize, currentPage);
    }
    render(): ReactNode {
        return(
            <>
            {this.props.isFetching && <Preloader />}
            <NewUsers {...this.props}
                changePage={this.changePage.bind(this)}
                follow={this.props.followThunk}
                unFollow={this.props.unFollowThunk} />
            </>
        );
    }
}
let mapStateToProps = (state: any) => ({
    users: state.userPage.users,
    totalUserCount: state.userPage.totalUserCount,
    pageSize: state.userPage.pageSize,
    currentPage: state.userPage.currentPage,
    isFetching: state.userPage.isFetching,
    fetchingInProgress: state.userPage.fetchingInProgress
});
export default compose(
    connect(mapStateToProps, {getCurrentPage, getCurrentUsersThunk, followThunk, unFollowThunk}),
    withAuthRedirect
)(UsersContainer2);