import React from 'react';
import { Link } from 'react-router-dom';
import style from './NewUsers.module.css';
const NewUsers = (props: any) => {
    let PageCount = Math.ceil(props.totalUserCount / props.pageSize);
    let pages = [];
    for (let i = 1; i <= PageCount; i++) {
        pages.push(i);
    }
    return(
        <div>
            <div style={{display: 'flex', gap: '5px'}}>
                {pages.map((item: any) => <button className={props.currentPage === item ? style.selected : ''} onClick={() => props.changePage(item)} key={item}>{item}</button>)}
            </div>
            {props.users.map((item: any) => (
                <div>
                <Link to={"/articles/" + item.id}><img src={item.photos ? item.photos.small : '#'} alt=''/></Link>
                <div>Name: {item.name}</div>
                <div>Status: {item.status}</div>
                {
                    item.followed
                    ? <div><button disabled={props.fetchingInProgress.some((i: any) => i === item.id)} onClick={() => props.unFollow(item.id)}>Unfollow</button></div>
                    : <div><button disabled={props.fetchingInProgress.some((i: any) => i === item.id)} onClick={() => props.follow(item.id)}>Follow</button></div>
                }
            </div>
            ))}
        </div>
    );
}
export default NewUsers;